.sentiment-filter-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  padding: 25px;
}

.sentiment-btns {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.sentiment-btns * {
  -webkit-transition: 0.15s ease-in;
  -o-transition: 0.15s ease-in;
  transition: 0.15s ease-in;
  border-radius: 2.5px;

}

.control-panel-btn.sent-pos {
  background-color: rgb(158,222,242);
  color: white;
  border-color: transparent;
}

.control-panel-btn.sent-neg {
  background-color: rgb(212,75,60);
  color: white;
  border-color: transparent;
}

.control-panel-btn.sent-neut {
  background-color: rgb(102, 255, 153);
  color: white;
  border-color: transparent;
}



